body {
  text-align: center;
  font-size: 30px;
  background-color: #5d1919;
}

h2 {
  color: teal;
}
.timer {
  display: flex;
  max-width: 50%;
  margin-right: auto;
  margin-left: auto;
  padding: 20px;
  justify-content: space-between;

  border: 2px solid #069797;
  border-radius: 5px;
}

.field {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.field > .label {
  color: #e59400;
}
